@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap');
.header{
    display: flex;
    align-items: center;
    color: white;
    /* top: 0;
    position: sticky;
    z-index: 2000; */
}
.header_left{
    flex:0.4;
}
.header_left >h1{
    text-align: center;
    font-family:"Poppins";
    padding: 20px 0px;
}
.header_left >h1 >span{
    color:aqua;
}
.header_right{
    display: flex;
    flex:0.6;
    justify-content:space-evenly;
    align-items: center;
}
.header_right >a >h4{
    cursor: pointer;
    font-size: 15px;
    transition:transform 200ms ease-in-out;
}
.header_right >a >h4:hover{
    transform: scale(1.19);
    color:aqua;
}
.join_button{
    background-color: aqua;
    color: black;
    padding: 10px;
    border-radius: 50px;
    cursor: pointer;
    transition:transform 200ms ease-in-out;
}
.join_button:hover{
    color: white;
    background-color:rgb(0, 92, 100);
    transform: scale(1.19);
}
@media screen and (max-width :600px) {
    .header_left {
        opacity:0;
    }
    .header_right{
        position: absolute;
        flex: auto;
    }
    .header_right a> h4{
        padding: 9px;
        font-size: 12px;
    }
}
@media screen and (max-width :800px) {
    .header_left {
        opacity:0;
    }
    .header_right{
        position: absolute;
        flex: auto;
    }
    .header_right a> h4{
        padding: 9px;
        font-size: 13px;
    }
}