@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
.contactcontainer{
    background-color: rgb(17, 17, 17);
    padding-bottom:100px;
    color: white;
}
.texts {
    text-align: center;
    padding-top: 100px;
    font-family: Poppins;
    line-height: 65px;
}
.texts >h2{
    font-weight: lighter;
}
.icons{
    font-size: 50px;
    display: flex;
    justify-content: center;
}
.icons>a >.icon1,.icon2,.icon3,.icon4{
    padding: 20px;
    color: rgb(255, 255, 255);
}
.icons>a >.icon1:hover{
    cursor: pointer;
    transform: scale(1.19);
    color: rgb(90, 13, 166);
}
.icons>a>.icon2:hover{
    cursor: pointer;
    transform: scale(1.19);
    color: rgb(255, 8, 140);
}
.icons>a>.icon3:hover{
    cursor: pointer;
    transform: scale(1.19);
    color: rgb(45, 72, 252);
}
.icon4:hover{
    cursor: pointer;
    transform: scale(1.19);
    color: rgb(252, 45, 45);
}