@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
.contactform {
    background-color: rgb(37, 37, 37);
    font-family: Poppins;
    font-size: 15px;
    text-align: center;
    padding: 100px;
}
.contactform>h1{
    color: white;
    padding-bottom: 50px;
}
.field>h2{
    color: white    ;
    font-weight: lighter;
    width: 170px;
}
.formm{
    justify-content: center;
}
.field>input{
    /* justify-content: center; */
    border-radius: 50px;
    margin-left: 50px;
    font-family: Poppins;
    border: 1px solid wheat;
    padding: 8px;
    padding-left:20px;
    color: white;
    background-color: rgba(0, 140, 140, 0);
}
.field{
    display: flex;
    justify-content: center;
    padding: 30px;
}
.field>textarea{
    color: white;
    margin-left: 50px;
    border-radius: 10px;
    font-family: Poppins;
    resize: none;
    border: 1px solid wheat;
    padding: 10px;
    width: 160px;
    height: 80px;
    background-color: rgba(0, 140, 140, 0);
}
.send_button{
    background-color: aqua;
    color: black;
    padding: 15px 20px;
    border: none;
    bottom: 10px;
    margin-top: 20px;
    margin-left: 270px;
    border-radius: 50px;
    cursor: pointer;
    transition:transform 200ms ease-in-out;
}
.send_button:hover{
    color: white;
    background-color:rgb(0, 92, 100);
    transform: scale(1.19);
}

@media screen and (max-width :800px) {
    .send_button{
        margin-top: 20px;
        margin-left: 20px;
    }
    .field{
        flex-direction: column;
        padding-bottom: 10px;
    }
    .field>textarea{
        /* width: 1000px; */
        margin-left: 0px;
    }
    .field>h2{
        padding-bottom: 10px;
        text-align: left;
    }
    .field>input{
        border-radius: 50px;
        margin-left: 0px;
    }
    .contactform>h1{
        color: white;
        padding-bottom: 20px;
    }
    .contactform {
        padding-top: 40px;
    }
}
