@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap');
.skillcontainer{
    background-color: rgb(17, 17, 17);
    display: flex;
    padding: 75px;
    align-items: center;
    justify-content: center;
    font-family: Poppins;
    font-weight:bold;
}
.skillsimg img{
    width: 500px;
    object-fit: contain;
    flex: 0.4;
    margin-right: 150px;
}  
.skillsset h4{
    font-size: 20px;
}
.skillscontainertext{
    color: white;
    max-width: 500px;
    flex:0.6;
    text-align: left;
}       
.progress1>span> .css-5xe99f-MuiLinearProgress-bar1{
    background-color: rgb(253, 98, 98) !important;
}
.progress2>span> .css-5xe99f-MuiLinearProgress-bar1{
    background-color: rgb(89, 212, 246) !important;
}
.progress3>span> .css-5xe99f-MuiLinearProgress-bar1{
    background-color: rgb(116, 255, 116) !important;
}
.progress4>span> .css-5xe99f-MuiLinearProgress-bar1{
    background-color: rgb(255, 190, 104) !important;
}
.progress5>span> .css-5xe99f-MuiLinearProgress-bar1{
    background-color: rgb(252, 255, 97) !important;
}
.MuiLinearProgress-colorPrimary{
    background-color: rgb(255, 255, 255) !important;
}
@media screen and (max-width:900px) {
    .skillsimg img{
        width:300px;
        padding-bottom: 50px;
        padding-left: 150px;
    }
    .skillcontainer{
        background-color: rgb(17, 17, 17);
        flex-direction:column;
    }
    .progress1{
        width: 300px;
    }
}