@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mr+Dafoe&display=swap');

.topcontents{
    color: white;
    font-family:Poppins;
}
.topcontents > .topcontent_contents >h1{
    color: white;
    font-family:Poppins;
    font-weight: lighter;
    font-size: 80px;
}
.topcontent_contents{
    position:absolute;
    top: 300px;
    right: 100px;
}
.topcontent_contents >p{
    text-align: left;
    font-size: 20px;
    padding-bottom: 20px;
}
.cvbtn,.workbtn{
    margin-top: 10px;
    background-color: aqua;
    font-size: 20px; 
    margin-right: 40px;
    border-radius: 20px;
    padding:10px 30px;
    cursor: pointer;
    border: 0;
    font-family: Poppins;
    font-weight: bolder;
    transition:transform 200ms ease-in-out;
}
.cvbtn:hover,.workbtn:hover{
    background-color:rgb(0, 92, 100);
    color: white;
    transform: scale(1.09);
}
@media screen and (max-width:800px) {
    .topcontents > .topcontent_contents >h1{
        font-size: 50px;
    }
    .topcontent_contents{
        top: 500px;
        right: 100px;
    }
    .cvbtn,.workbtn{
        margin-top: 10px;
        font-size: 15px; 
        margin-right: 20px;
        padding:10px 10px;
    }
    .cvbtn:hover,.workbtn:hover{
        background-color:rgb(0, 92, 100);
        color: white;
        transform: scale(1.09);
    }
}
@media screen and (max-width:400px) {
    .topcontents > .topcontent_contents >h1{
        font-size: 50px;
    }
    .topcontent_contents{
        top: 400px;
        right: 100px;
    }
    .cvbtn,.workbtn{
        font-size: 15px; 
    }
}