@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
.projectcontainer{
    background-color: rgb(38, 38, 38);
    padding-top: 50px;
    align-items: center;
}
.projectcontainer > h1{
    color: white;
    font-family: Poppins;
    font-weight: bold;
    text-align: center;
}
img{
    width: 400px;
    padding: 20px;
    border-radius: 50px;
}
.projects{
    justify-content: center;
}
.projectstab{
    display: flex;
    flex-direction:row;
    justify-content: center;
    align-items: center;
    padding: 100px 0px;
}
.project:hover{
    transform: scale(1.09);
    cursor: pointer;
    justify-items: center;
    align-items: center;
}
.prjoverlay{
    position: relative;
    display: inline-block;
    margin: 4px;
}
.prjoverlay:hover :not(.prjdesc,span){
    filter: blur(8px);
}
.prjoverlay > p>span{
    font-size: 30px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: center;
    padding-left: 100px;
    color: red;
}
.prjdesc{ 
    width: 350px;
    height: 200px;
    padding: 30px 30px;
    position: absolute;
    top: 5%;
    z-index: 1;
    left: 5%;
    font-family:Poppins;
    opacity: 0;
    font-weight: bold;
    color: black;
    font-size: 20px;
    letter-spacing: 1px;
    line-height: 25px;
}
.prjdesc:hover{
    opacity: 1;
}
@media screen and (max-width :800px) {
    .projectstab{
        flex-direction:column;
        justify-content: center;
        padding: 100px 0px;
    }
    img{
        width: 300px;
        padding: 20px;
    }
}
